


.form_title {
	color: #646cff;
	margin: 15px 7px 5px;
	font-weight: 600;
	font-size: 22px;
	line-height: 23px;
}


.cmp_dtls{
	padding: 15px 3px;
}


.cmp_desc{
	margin-top: 25px;
	padding: 15px 3px;
	border-top: 1px solid #dadce0;
	text-align: left;
}

.cmp_desc .description{
	margin-left: 12px;
}


.desc_lbl{
	margin-right: 8px;
}

.hdr_bar{
	padding: 10px 7px;
	border-radius: 8px;
}






.boxAddAcc{
	padding: 16px;
    border: 1px solid var(--color_text_dad);
    border-radius: 8px;
}



.icon svg{
	cursor: pointer;
}


.tex_left{
	text-align: left;
}













.account_item_box{
	padding: 16px;
	margin: 2px;
}

.account_item_box:hover {
	background-color: #f1f1f1;
	cursor: pointer;
	border-radius: 7px;
}


.account_item_box .code{
	line-height: 21px;
	margin-right: 11px;
}

.account_item_box .name{
	line-height: 21px;
	
}

.account_item_box:hover .code{
	color: #646cff;
	font-weight: 600;
	font-size: 19px;
}

.account_item_box:hover .name{
	color: #646cff;
}




.acc_dtls{
	padding: 0 11px 11px;
	margin-left: 30px;
}

.acc_dtls .code{
	margin-right: 12px;
	font-weight: 600;
	font-size: 18px;
	line-height: 18px;
}
.acc_dtls .name{
	font-weight: 500;
	line-height: 18px;
}

.comprobante_id{
	color: #646cff;
	font-weight: 600;
	cursor: pointer;
}


.scoll_CP_Height{
	height: calc(100vh - 330px);
}



.menu_pay._floating{
	min-width: 720px;
}

.menu_collect._floating{
	min-width: 720px;
}